* {
    margin: 0;
    padding: 0;
    outline: 0;
    font-family: bornomala;
    box-sizing: border-box;
}

body {
    background-color: #f9d270;
    font-size: 1rem;
    color: #3a3a3a;
}

.navbar {
    padding: .5rem 0;
    display: flex;
    justify-content: space-between;
    text-align: center;
    margin-bottom: 2rem;
    border-bottom: 1px solid #3a3a3a;
}

.navbar a {
    text-decoration: none;
    font-size: 1.2rem;
    color: #3a3a3a;
}

h1, h2 {
    font-weight: 400;
}

.container {
    width: 1490px;
    margin: 0 auto;
}

.row {
    display: flex;
    justify-content: space-between;
}

.row .form-group {
    margin-right: 1rem;
}

.row .form-group:last-child {
    margin-right: 0px;
}

.mr-3 {
    margin-right: 1rem;
}

.mb-3 {
    margin-bottom: 1rem;
}

.form-group {
    margin-bottom: 1rem;
    width: 100%;
}

.form-group label {
    display: inline-block;
    margin-bottom: .3rem;
    font-size: 17px;
}

.form-group .input-group {
    display: flex;
}

.form-group .input-group .form-control:first-child {
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
}

.form-group .input-group .form-control:first-child {
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
}

.form-group .input-group .form-control:last-child {
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
    border-left: 1px solid #e6e6e6;
}

.form-control {
    padding: .5rem 1rem;
    font-size: 18px;
    border: none;
    width: 100%;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

.form-control.invalid {
    box-shadow: 0 0 0 0.2rem #ff7979;
}

.form-control:focus {
    color: #495057;
    background-color: #fff;
    border-color: #ffc107;
    outline: 0;
    box-shadow: 0 0 0 0.25rem #e2c059;
}

.btn {
    padding: .5rem 1rem;
    font-size: 1rem;
    border: none;
    cursor: pointer;
    color: #fff;
    transition: .3s ease;
    border-radius: 0;
}

.btn-sm {
    padding: .3rem 1rem;
    font-size: 16px;
}

.btn-success {
    background-color: #1daa00;
}
.btn-success:hover {
    background-color: #178a00;
}

.btn-primary {
    background-color: #008bea;
}

.btn-primary:hover {
    background-color: #0073c0;
}

.btn-danger {
    background-color: #fc2222;
}

.btn-danger:hover {
    background-color: #e20101;
}

.table {
    width: 100%;
    border-collapse: collapse;
    margin: 1rem 0;
    overflow: hidden;
}

.table tr.head {
    background-color: #3a3a3a;
    border-top-left-radius: 5px;
}

.table tr.head th {
    color: #fff;
    font-size: 1.1rem;
    padding: .5rem 0;
    text-align: center;
    font-weight: 400;
}

.table tr {
    background-color: #fff;
    border-bottom: 1px solid #e2e2e2;
}

.table tr:first-child {
    border-bottom: none;
}

.table tr:last-child {
    border-bottom: 2px solid #3a3a3a;
}

.table tr td {
    font-size: 1.1rem;
    padding: .5rem 0;
    text-align: center;
}

.modal {
    position: fixed;
    bottom: 0; 
    top: 0;
    right: 0; 
    left: 0;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: all 0.3s ease-in-out;
    pointer-events: none;
}

.modal.show {
    opacity: 1;
    pointer-events: visible;
}

.modal-content {
    padding: 2rem 3rem;
    /* border-radius: .5rem; */
    background-color: #ffe090;
    transform: translateY(-200px);
    transition: all 0.3s ease-in-out;
}

.modal-body {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 15px;
}

.modal.show .modal-content {
    transform: translateY(0);
}

@media screen and (max-width: 500px) {
    .container {
        width: 100%;
        padding: 0 15px;
        overflow: hidden;
    }

    .row {
        flex-direction: column;
    }

    .modal-body {
        grid-template-columns: 1fr;
    }

    .table-container {
        overflow-x: scroll;
    }
    
    .table {
        width: 1200px;
        margin-bottom: 0;
    }
}